
import React from "react";
import styled, { keyframes } from 'styled-components';
import { pulse } from 'react-animations';
// import { fadeIn} from 'react-animations';
import resume from "./media/Resume-MichaelKisida.pdf";
import headshot from "./img/headshot.jpg";
import { motion } from "framer-motion"

// const FadeInAnimation = keyframes`${fadeIn}`;
// const FadeInDiv = styled.div`
//   animation: 2s ${FadeInAnimation};
// `;

const PulseAnimation = keyframes`${pulse}`;
const PulseDiv = styled.div`
  animation: infinite 2s ${PulseAnimation};
`;
export default function About() {


    return (
        <section className="font-inter bg-gray-900"  id="about">
           {/* <section className="font-inter bg-about-campus bg-no-repeat bg-cover bg-blend-overlay bg-gray-800"  id="about"> */}

           
            <div className="ml-60 flex flex-wrap min-h-screen max-w-screen-sm items-center">
            <motion.div
               initial={{ opacity: 0, scale: 0.5 }}
               animate={{ opacity: 1, scale: 1 }}
               transition={{ duration:.7}} >
                
                <div>
                <div className="mb-6">
                <img className="w-1/2 rounded-full border-gradient-br-custom-gradient-gray-900 gradient-border-4" src={headshot} alt="headshot"/>
                </div>
                    <h1 className="md:text-7xl sm:text-5xl mb-6 font-black text-transparent bg-clip-text bg-gradient-to-r from-custom-blue to-custom-purple text-center">
                        Michael Kisida
                    </h1>
                    <p className=" md:text-xl leading-relaxed text-white font-bold">
                        Information Technology<br></br>University of Central Florida
                    </p>
                    <p className="mb-5 md:text-xl leading-relaxed text-gray-200 font-regular ">
                        Graduation Date: December 2023
                    </p>
                    <div className="">
                    <a href={resume} target="_blank">
                        <button type="button" className="inline-flex items-center px-6 py-2 border-gradient-br-custom-gradient-gray-900 hover:bg-gradient-to-r from-custom-blue to-custom-purple  gradient-border-2 rounded-full text-gray-100 text-sm font-bold">
                        Resume</button></a>
                        
                    </div>
                    
                </div>
                </motion.div>
            </div>
        </section>
    );
}